.style_card_apresentacao {
  background-color: #ffffff;
  padding: 3.5rem;
  margin-top: -2rem;
  border-radius: 6px;
  box-shadow: 1px 1px 29px -2px rgb(0 0 0 / 52%);
  -webkit-box-shadow: 1px 1px 29px -2px rgb(0 0 0 / 52%);
}

.shortly-logo {
  width: 50%;
  max-width: 300px;
  margin-top: -2rem;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.input-url {
  width: 70%;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  margin-left: auto;
  margin-right: auto;
}

.button-url {
  text-decoration: none;
  color: #ffffff;
  background-color: #24d3ee;
  text-align: center;
  transition: all 0.2s;
  border: none;
}

.button-url:hover {
  background-color: #2cb7cc;
}

.button-url:focus {
  background-color: #27a4b8;
}

.button-url:active {
  background-color: #27a4b8;
}

.text-url {
  color: #646464;
  margin-left: 1rem;
  margin-right: 1rem;
  font-size: medium;
  text-align: center;
}

.style-number-count {
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #43507a;
  font-size: 4.4em; color: #2f395c;
}

.footer {
  position: absolute !important;
}